@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);

$titleFont: "Open Sand Condensed";

body {
  background-color: white;
}

.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  .card {
    padding: 10px;
    display: flex;
    background-color: white;
    border-radius: 1px;
    box-shadow: 1px 1px 1px 1px #ccc;
    width: 300px;
    flex-wrap: wrap;
    height: 150px;
    align-items: baseline;

    flex-basis: auto;
    margin: 20px;
    transition: background-color 0.5s ease-out;

    a {
      text-decoration: none;
      color: black;
    }

    &:hover {
      transform: translateY(0.5px);
    }

    .title {
      font-size: 15px;
    }

    .labels {
      align-self: flex-end;
      display: flex;
      align-items: baseline;

      span {
        display: flex;
        opacity: 1;
        color: whitesmoke;
        font-size: 10px;
        text-align: center;
        padding: 7px;
      }

      .keywords {
        border-radius: 25px;
        background-color: blue;
        opacity: 0.5;
      }

      .department {
        border-radius: 25px;
        background-color: red;
        opacity: 0.6;
      }

      .year {
        border-radius: 18px;
        background-color: green;
        opacity: 0.7;
      }
    }
  }
}
